export default {
  addAlert({ commit, dispatch }, { text, type, timeout = 2500 }) {
    const id = Math.floor(Math.random() * Date.now());
    const alert = {
      text,
      type,
      id,
    };
    commit("ADD_ALERT", alert);
    setTimeout(() => {
      dispatch("deleteAlert", id);
    }, timeout);
  },
  deleteAlert({ commit }, id) {
    commit("DELETE_ALERT", id);
  },
};
