import axios from "axios";

const url = "https://peaceful-shore-13462.herokuapp.com/api";
export default {
  fetchProfile({ commit }) {
    return axios.get(url + "/profile?user=emir").then((res) => {
      commit("SET_PROFILE", res.data);
    });
  },
  uploadImage(_, file) {
    return axios.post(url + "/upload?user=emir", file).then((res) => {
      return res.data;
    });
  },
  updateProfile({ dispatch, state, rootState }) {
    return axios
      .put(url + "/store-profile-details", state.profile)
      .then((res) => {
        dispatch("fetchProfile");
        dispatch(
          "alerts/addAlert",
          {
            text: "Profile updated",
            type: "success",
          },
          { root: true }
        );
      });
  },
  deleteImage() {},
};
