export default {
  SET_PROFILE(state, profile) {
    state.profile = profile;
  },
  SET_HERO_NAME(state, name) {
    state.profile.hero.name = name;
  },
  SET_ABOUT_NAME(state, name) {
    state.profile.about.contactDetails.name = name;
  },
  SET_ABOUT_ABOUT_ME(state, aboutMe) {
    state.profile.about.aboutMe = aboutMe;
  },
  SET_ABOUT_PHOTO(state, photo) {
    state.profile.about.photoURL = photo;
  },
  SET_ABOUT_LOCATION(state, location) {
    state.profile.about.contactDetails.location = location;
  },
  SET_HERO_PHONE_NUMBER(state, phoneNumber) {
    state.profile.about.contactDetails.phoneNumber = phoneNumber;
  },
};
